import React, { useState, useEffect, useRef, useCallback } from 'react';
import { FaPhone, FaMapMarkerAlt, FaUser, FaBars, FaTimes, FaHome, FaBuilding, FaChartLine, FaKey, FaChevronDown, FaEnvelope } from 'react-icons/fa';
import './header.css';

const Header: React.FC = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isSticky, setIsSticky] = useState(false);
  const [topBarHeight, setTopBarHeight] = useState(0);
  const [isServicesOpen, setIsServicesOpen] = useState(false);
  const isStickyRef = useRef(false);
  const topBarHeightRef = useRef(0);
  const rafRef = useRef<number | null>(null);
  const servicesTimeoutRef = useRef<NodeJS.Timeout | null>(null);

  useEffect(() => {
    // Calculate top bar height for scroll calculations
    const topBar = document.querySelector('.top-bar');
    if (topBar) {
      topBarHeightRef.current = topBar.clientHeight;
      setTopBarHeight(topBar.clientHeight);
    }
  }, []);

  useEffect(() => {
    const handleScroll = () => {
      // Cancel any pending animation frame
      if (rafRef.current) {
        cancelAnimationFrame(rafRef.current);
      }

      // Use requestAnimationFrame to batch the state update with the next paint
      rafRef.current = requestAnimationFrame(() => {
        const shouldBeSticky = window.scrollY > topBarHeightRef.current;
        
        // Only update state if the sticky status has changed
        if (shouldBeSticky !== isStickyRef.current) {
          isStickyRef.current = shouldBeSticky;
          setIsSticky(shouldBeSticky);
        }
      });
    };

    // Use passive listener for better scroll performance
    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => {
      window.removeEventListener('scroll', handleScroll);
      if (rafRef.current) {
        cancelAnimationFrame(rafRef.current);
      }
    };
  }, []);

  useEffect(() => {
    // Prevent body scroll when mobile menu is open
    if (isMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }

    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isMenuOpen]);

  const servicesItems = [
    {
      title: 'Residential Management',
      description: 'Single-family & multifamily management',
      icon: <FaHome />,
      href: '/residential-management'
    },
    {
      title: 'Commercial Management', 
      description: 'Professional commercial property oversight',
      icon: <FaBuilding />,
      href: '/commercial-property-management'
    },
    {
      title: 'Investor Corner',
      description: 'Reports, ROI planning, and strategies',
      icon: <FaChartLine />,
      href: '/investors-corner'
    },
    {
      title: 'Find a Rental Home',
      description: 'Browse available rentals',
      icon: <FaKey />,
      href: '#rentals'
    }
  ];

  const navLinks = [
    { label: 'Home', href: '/' },
    { label: 'Services', href: '/#services', hasDropdown: true },
    { label: 'About', href: '/#about' },
    { label: 'Contact', href: '/contact' },
  ];

  const handleServicesMouseEnter = () => {
    if (servicesTimeoutRef.current) {
      clearTimeout(servicesTimeoutRef.current);
    }
    setIsServicesOpen(true);
  };

  const handleServicesMouseLeave = () => {
    servicesTimeoutRef.current = setTimeout(() => {
      setIsServicesOpen(false);
    }, 150);
  };

  return (
    <>
      {/* Top Bar */}
      <div className={`top-bar ${isSticky ? 'top-bar-hidden' : ''}`}>
        <div className="container">
          <div className="top-bar-content">
            <div className="top-bar-left">
              <a href="tel:+16035083888" className="top-bar-item" aria-label="Call us at (603) 508-3888">
                <FaPhone className="top-bar-icon" aria-hidden="true" />
                <span className="top-bar-text">(603) 508-3888</span>
              </a>
              <div className="top-bar-item">
                <FaMapMarkerAlt className="top-bar-icon" aria-hidden="true" />
                <span className="top-bar-text">not found null</span>
              </div>
            </div>
            <div className="top-bar-right">
              <a href="mailto:sales@marketstreetpropertymanagement.com" className="top-bar-item" aria-label="Email us">
                <FaEnvelope className="top-bar-icon" aria-hidden="true" />
                <span className="top-bar-text">sales@marketstreetpropertymanagement.com</span>
              </a>
            </div>
          </div>
        </div>
      </div>

      {/* Main Navbar */}
      <nav className={`navbar ${isSticky ? 'navbar-sticky' : ''}`} aria-label="Main navigation">
        <div className="container">
          {/* Logo */}
          <a href="#" className="logo" aria-label="Market Street Property Management Home">
           <img src="https://ik.imagekit.io/dloitl7fd8/ChatGPT%20Image%20Jan%2023,%202026,%2002_17_06%20AM.png" alt="" />
          </a>

          {/* Desktop Navigation */}
          <div className="nav-desktop">
            <ul className="nav-links" role="menubar">
              {navLinks.map((link) => (
                <li 
                  key={link.label} 
                  role="none"
                  className={link.hasDropdown ? 'nav-item-dropdown' : ''}
                  onMouseEnter={link.hasDropdown ? handleServicesMouseEnter : undefined}
                  onMouseLeave={link.hasDropdown ? handleServicesMouseLeave : undefined}
                >
                  <a 
                    href={link.href} 
                    className="nav-link"
                    role="menuitem"
                    aria-haspopup={link.hasDropdown ? 'true' : undefined}
                    aria-expanded={link.hasDropdown ? isServicesOpen : undefined}
                  >
                    {link.label}
                    {link.hasDropdown && <FaChevronDown className={`dropdown-arrow ${isServicesOpen ? 'dropdown-arrow-open' : ''}`} />}
                  </a>
                  
                  {/* Services Dropdown */}
                  {link.hasDropdown && (
                    <div className={`services-dropdown ${isServicesOpen ? 'services-dropdown-open' : ''}`}>
                      <div className="services-dropdown-content">
                        <div className="services-grid">
                          {servicesItems.map((service, index) => (
                            <a 
                              key={index}
                              href={service.href}
                              className="service-item"
                              onClick={service.href === '#rentals' ? (e) => {
                                e.preventDefault();
                                document.getElementById('rentals')?.scrollIntoView({ behavior: 'smooth' });
                                setIsServicesOpen(false);
                              } : () => setIsServicesOpen(false)}
                            >
                              <div className="service-icon">
                                {service.icon}
                              </div>
                              <div className="service-content">
                                <h4 className="service-title">{service.title}</h4>
                                <p className="service-description">{service.description}</p>
                              </div>
                            </a>
                          ))}
                        </div>
                      </div>
                    </div>
                  )}
                </li>
              ))}
            </ul>
            <a href="/contact" className="cta-button-head" aria-label="Get a free quote">
              Get Free Quote
            </a>
          </div>

          {/* Mobile Menu Button */}
          <button
            className="mobile-menu-button"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            aria-label={isMenuOpen ? 'Close menu' : 'Open menu'}
            aria-expanded={isMenuOpen}
            aria-controls="mobile-menu"
          >
            {isMenuOpen ? <FaTimes aria-hidden="true" /> : <FaBars aria-hidden="true" />}
          </button>
        </div>

        {/* Mobile Menu Overlay */}
        <div
          id="mobile-menu"
          className={`mobile-menu-overlay ${isMenuOpen ? 'mobile-menu-open' : ''}`}
          aria-hidden={!isMenuOpen}
        >
          <div className="mobile-menu-content">
            <ul className="mobile-nav-links" role="menu">
              {navLinks.map((link) => (
                <li key={link.label} role="none">
                  {link.hasDropdown ? (
                    <div className="mobile-dropdown">
                      <button
                        className="mobile-nav-link mobile-dropdown-toggle"
                        onClick={() => setIsServicesOpen(!isServicesOpen)}
                        aria-expanded={isServicesOpen}
                      >
                        {link.label}
                        <FaChevronDown className={`mobile-dropdown-arrow ${isServicesOpen ? 'mobile-dropdown-arrow-open' : ''}`} />
                      </button>
                      <div className={`mobile-services-dropdown ${isServicesOpen ? 'mobile-services-dropdown-open' : ''}`}>
                        {servicesItems.map((service, index) => (
                          <a
                            key={index}
                            href={service.href}
                            className="mobile-service-item"
                            onClick={(e) => {
                              if (service.href === '#rentals') {
                                e.preventDefault();
                                document.getElementById('rentals')?.scrollIntoView({ behavior: 'smooth' });
                              }
                              setIsMenuOpen(false);
                              setIsServicesOpen(false);
                            }}
                          >
                            <div className="mobile-service-icon">{service.icon}</div>
                            <span>{service.title}</span>
                          </a>
                        ))}
                      </div>
                    </div>
                  ) : (
                    <a
                      href={link.href}
                      className="mobile-nav-link"
                      role="menuitem"
                      onClick={() => setIsMenuOpen(false)}
                    >
                      {link.label}
                    </a>
                  )}
                </li>
              ))}
            </ul>
            <div className="mobile-menu-bottom">
              <a href="/contact" className="mobile-cta-button" onClick={() => setIsMenuOpen(false)}>
                Get Free Quote
              </a>
              <div className="mobile-contact-info">
                <div className="mobile-contact-item">
                  <FaPhone aria-hidden="true" />
                  <span>(603) 508-3888</span>
                </div>
                <div className="mobile-contact-item">
                  <FaMapMarkerAlt aria-hidden="true" />
                  <span>not found null</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Header;